import React, { useEffect, useState } from "react";
import Pagina from "../layouts/Pagina.jsx";
import Rua from "../telas/elementos/Rua.jsx";
import { Card, CardText, Container, Form, InputGroup } from "react-bootstrap";
import { useSelector } from "react-redux";

export default function Busca(props) {
    const temp = useSelector((state) => state.ruas) // Inicializa como um array vazio
    const listaRuas = Array.from(temp); 
    const [pesquisa, setPesquisa] = useState("");
    const [busca, setBusca] = useState([]);

    useEffect(() => {
        let buscaFiltrada = listaRuas?.filter((rua) => {
            if (!rua) return false; // Verifica se 'rua' existe
            let nome = rua.nome;
            let pesq = pesquisa;
            const ruaLC = nome.toLowerCase(); // Converte para minsculas
            const pesquisaLC = pesq.toLowerCase(); // Coloca a pesquisa em minsculas
            return ruaLC.includes(pesquisaLC); // Verifica se o nome da rua inclui a pesquisa
        });
        setBusca(buscaFiltrada); // Atualiza o estado da busca
    }, [listaRuas, pesquisa]); // Executa sempre que listaRuas ou pesquisa mudarem

    function manipularMudanca(evento) {
        setPesquisa(evento.target.value); // Atualiza o estado da pesquisa
    }

    return (
        <Pagina>
            <Container>
                <Card style={{ width: "65rem" }} className="mx-auto m-4 pt-4 text-center bg-body-tertiary">
                    <InputGroup className="ps-4 pe-4 pb-4 pt-1">
                        <Form.Control
                            type="text"
                            id="pesquisa"
                            placeholder="Nome da rua"
                            value={pesquisa}
                            onChange={manipularMudanca} // Usando a funo diretamente
                        />
                    </InputGroup>
                </Card>
                <Card style={{ width: "65rem" }} className="mx-auto">
                    <CardText className="p-4 text-center">
                        {   busca.length ? (
                            busca.map((rua) => (
                                <Rua rua={rua} /> // Adicionando a chave nica para cada item
                            ))
                        ) : (
                            "Nenhum resultado encontrado."
                        )}
                    </CardText>
                </Card>
            </Container>
        </Pagina>
    );
}
