const estadoInicial = "";

// Reducer para lidar com as aes de logar e deslogar
const loginReducer = (state = estadoInicial, action) => {
  switch (action.type) {
    case "LOGAR":
      return action.payload; // Armazena o nome do usurio no estado
    case "DESLOGAR":
      return ""; // Limpa o estado ao deslogar
    default:
      return state;
  }
};

export default loginReducer;
